/**** ------------------------- scrollbar -----------------***/

/* width */
::-webkit-scrollbar {
  display: none !important;
  /* width: 5px; */
}

/* Track */
::-webkit-scrollbar-track {
  /* background: transparent; */
  background: inset 0 0 5px grey;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  /* background: transparent; */
  background: var(--pinkShade);
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: var(--violetShade);
}

